.button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 30px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.button:focus {
    outline: none;
}

.button_line {
    width: 30px;
    height: 2px;
    background: #eb8c34;
}